import React, { Component } from 'react';
import { ResponsiveLine } from '@nivo/line'

function LineChart(props) {
    // const data = props.data;
    const data = [{
        id: 'crypto',
        color: '#03a9f4',
        data: [
            { x: 0, y: 10},
            { x: 10, y: 50 },
            { x: 20, y: 20 },
            { x: 30, y: 30 },
            { x: 40, y: 80 },
        ]
    }]
    return (
        <ResponsiveLine
            data={data}
            // margin={{
            //     "top": 50,
            //     "right": 110,
            //     "bottom": 50,
            //     "left": 60
            // }}
            colorBy={function (c) { return c.color }}
            lineWidth={3}
            height={125}
            yScale={{
                "type": 'linear',
                "min": "auto",
                "max": "auto"
            }}
            enableGridX={false}
            enableGridY={false}
            isInteractive={false}
            enableDots={false}
            enableArea={true}
        />
    )
}

export default LineChart;